* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font: 16px "Roboto", Helvetica, sans-serif;
  overflow-x: hidden;
}

.apply_display_flex {
  display: flex;
}

.apply_display_flex_sb {
  display: flex;
  justify-content: space-between;
}

input[type="submit"],
input[type="button"],
button,
a,
li {
  cursor: pointer;
}

/* 1주차 다해 - css grid 시스템을 이용한 전체 holy grail layout */

#my-header {
  grid-area: header;
}
#my-sidebar {
  grid-area: sidebar;
}
#my-main {
  grid-area: main;
}
#my-footer {
  grid-area: footer;
}

.grid-container {
  display: grid;
  min-width: 100vw;
  min-height: 100vh;
  grid-template-rows: 50px auto 80px;
  grid-template-columns: 250px auto;
  grid-template-areas:
    "header  header"
    "sidebar main  "
    "sidebar footer";
}

@media only screen and (max-width: 1280px) {
  .grid-container {
    grid-template-areas:
      "header"
      "sidebar main"
      "footer";

    grid-template-rows: 50px auto 80px;
    grid-template-columns: 250px auto;
  }
}

@media only screen and (max-width: 768px) {
  .grid-container {
    grid-template-areas:
      "header"
      "sidebar main"
      "footer";

    grid-template-rows: 50px auto 60px;
    grid-template-columns: 0px auto;
  }
}

/* 1주차 다해 - Header 컴포넌트의 css 스타일 적용 */

#my-header {
  background-color: #1465b1;
  display: flex;
}

#my-header a {
  text-align: center;
}

.logo-container {
  line-height: 50px;
  width: 250px;
  background-color: #1e458e;
}

.logo-image {
  width: 100px;
}

.toggle-btn {
  line-height: 2;
  width: 60px;
  color: #fff;
  font-size: 24px;
}

.toggle-btn:hover {
  background-color: #1e458e;
  color: #fff;
}

@media only screen and (max-width: 768px) {
  #my-header {
    background-color: #1e458e;
  }

  #my-header a {
    display: block;
  }

  .logo-container {
    margin: 0 auto;
  }

  .toggle-btn {
    position: absolute;
    top: 0;
    left: 0;
  }
}

/* 1주차 다해 - Sidebar 컴포넌트 css 스타일 적용 */

#my-sidebar {
  background-color: rgb(37, 40, 42);
  overflow-x: hidden;
}

.show-my-sidebar {
  width: 250px;
  z-index: 999;
  position: relative;
  top: 0;
  left: 0;
}

.hide-my-sidebar {
  display: none;
}

@media only screen and (max-width: 768px) {
  .show-my-sidebar {
    width: 200px;
    z-index: 999;
    position: relative;
    top: 0;
    left: 0;
  }
}

/* 1주차 다해 - 사이드바 내부 Status 컴포넌트 css 스타일 적용 */

.status {
  padding: 40px 0;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.btn-user {
  font-size: 12px;
  padding: 6px 12px;
  border-radius: 4px;
  border: 2px solid rgb(255, 255, 255, 0.5);
  background-color: rgb(37, 40, 42);
  color: rgb(255, 255, 255, 0.5);
