}

/* 1주차 다해 - 사이드바 내부의 MenuList 컴포넌트 css 스타일 적용 */

.menu-list {
  list-style: none;
  -webkit-padding-start: 0px;
          padding-inline-start: 0px;
}

.menu-list li {
  padding: 16px 16px;
  text-decoration: none;
  color: rgb(255, 255, 255);
}

.menu-list li:hover {
