* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font: 16px "Roboto", Helvetica, sans-serif;
  overflow-x: hidden;
}

.apply_display_flex {
  display: flex;
}

.apply_display_flex_sb {
  display: flex;
  justify-content: space-between;
}

input[type="submit"],
input[type="button"],
button,
a,
li {
  cursor: pointer;
}
